  dualRenderHomeToViewAnimation(): DualRenderOutput {
    const { appHeight, screen } = this.state;
    const elapsedSeconds = getElapsedSeconds(
      screen.currentTimestamp,
      screen.animationStartTime
    );
    const homeScreenDy = Math.max(
      -(appHeight * elapsedSeconds) / SCREEN_TRANSITION_ANIMATION_SECONDS,
      -appHeight
    );

    const battleEvents = this.getCurrentLevelBattleEvents();
    const battleElapsedSeconds =
      screen.animationKind === AnimationKind.PlayBattle
        ? screen.battleAnimationOffsetSeconds + elapsedSeconds
        : screen.animationKind === AnimationKind.PauseBattle
        ? screen.battleAnimationOffsetSeconds
        : 0;
    const battleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level),
      battleEvents,
      battleElapsedSeconds
    );

    const reactElement = (
      <div className="App">
        <div className="AppMain">
          <svg
            className="MainSvg"
            width={this.state.appWidth}
            height={appHeight}
          ></svg>
        </div>
      </div>
    );

    const canvasElement: CanvasElement = {
      kind: CanvasElementKind.Group,
      children: [
        {
          kind: CanvasElementKind.Group,
          transform: [{ kind: TransformKind.Translate, x: 0, y: homeScreenDy }],
          children: [this.renderHomeScreenCanvasElement()],
        },
        this.renderBattleAndOutcomeScreenCanvasElement(
          homeScreenDy,
          screen.blockEditor,
          screen.level,
          battleElapsedSeconds,
          battleAnimationData
        ),
      ],
    };

    return { reactElement, canvasElement };
  }

  dualRenderEditToViewAnimation(): DualRenderOutput {
    const { appHeight, screen } = this.state;
    const elapsedSeconds = getElapsedSeconds(
      screen.currentTimestamp,
      screen.animationStartTime
    );
    const blockEditorDy = Math.max(
      -(appHeight * elapsedSeconds) / SCREEN_TRANSITION_ANIMATION_SECONDS,
      -appHeight
    );

    const battleEvents = this.getCurrentLevelBattleEvents();
    const battleElapsedSeconds =
      screen.animationKind === AnimationKind.PlayBattle
        ? screen.battleAnimationOffsetSeconds + elapsedSeconds
        : screen.animationKind === AnimationKind.PauseBattle
        ? screen.battleAnimationOffsetSeconds
        : 0;
    const battleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level),
      battleEvents,
      battleElapsedSeconds
    );

    const blockEditorProps: BlockEditorProps<BlockKind> = {
      config: this.getMainBlockEditorConfig(),
      state: screen.blockEditor,

      measureText: this.measureSvgText,
      setState: this.setBlockEditorState,
    };

    const reactElement = (
      <div className="App">
        <div className="AppMain">
          <svg
            className="MainSvg"
            width={this.state.appWidth}
            height={appHeight}
          >
            <g transform={`translate(0 ${blockEditorDy})`}>
              <BlockEditor {...blockEditorProps} />
            </g>
          </svg>
        </div>
      </div>
    );

    const canvasElement: CanvasElement = {
      kind: CanvasElementKind.Group,
      children: [
        this.renderBattleAndOutcomeScreenCanvasElement(
          blockEditorDy,
          screen.blockEditor,
          screen.level,
          battleElapsedSeconds,
          battleAnimationData
        ),
        this.renderPuzzleScreenButtonsCanvasElement(),
      ],
    };

    return { reactElement, canvasElement };
  }

  dualRenderViewToEditAnimation(): DualRenderOutput {
    const { appHeight, screen } = this.state;
    const elapsedSeconds = getElapsedSeconds(
      screen.currentTimestamp,
      screen.animationStartTime
    );
    const blockEditorDy = Math.min(
      (appHeight * elapsedSeconds) / SCREEN_TRANSITION_ANIMATION_SECONDS -
        appHeight,
      0
    );

    const battleEvents = this.getCurrentLevelBattleEvents();
    const battleElapsedSeconds =
      screen.animationKind === AnimationKind.PlayBattle
        ? screen.battleAnimationOffsetSeconds + elapsedSeconds
        : screen.animationKind === AnimationKind.PauseBattle
        ? screen.battleAnimationOffsetSeconds
        : 0;
    const battleAnimationData = getBattleAnimationData(
      getInitialLevelState(screen.level),
      battleEvents,
      battleElapsedSeconds
    );

    const blockEditorProps: BlockEditorProps<BlockKind> = {
      config: this.getMainBlockEditorConfig(),
      state: screen.blockEditor,

      measureText: this.measureSvgText,
      setState: this.setBlockEditorState,
    };

    const reactElement = (
      <div className="App">
        <div className="AppMain">
          <svg
            className="MainSvg"
            width={this.state.appWidth}
            height={appHeight}
          >
            <g transform={`translate(0 ${blockEditorDy})`}>
              <BlockEditor {...blockEditorProps} />
            </g>
          </svg>
        </div>
      </div>
    );

    const canvasElement: CanvasElement = {
      kind: CanvasElementKind.Group,
      children: [
        this.renderBattleAndOutcomeScreenCanvasElement(
          blockEditorDy,
          screen.blockEditor,
          screen.level,
          battleElapsedSeconds,
          battleAnimationData
        ),
        this.renderPuzzleScreenButtonsCanvasElement(),
      ],
    };

    return { reactElement, canvasElement };
  }

