function isWithinButtonWithNonextendedHomePosition(
  clientPosition: Vec2,
  bboxInNonextendedHomeCoords: Vec2,
  buttonImage: HTMLImageElement,
  state: State,
  resources: ButtonClickDetectorResources
): boolean {
  const MAIN_DIMENSIONS: Dimensions = {
    width: 2039,
    height: 1447,
  };
  const minimumHorizontalScale = state.appWidth / MAIN_DIMENSIONS.width;
  const minimumVerticalScale = state.appHeight / MAIN_DIMENSIONS.height;
  const scale = Math.min(minimumHorizontalScale, minimumVerticalScale);
  const mainRbox: Rectangle = ((): Rectangle => {
    const scaledWidth = MAIN_DIMENSIONS.width * scale;
    const scaledHeight = MAIN_DIMENSIONS.height * scale;
    const x = (state.appWidth - scaledWidth) / 2;
    const y = (state.appHeight - scaledHeight) / 1;
    return {
      topLeft: { x, y },
      dimensions: { width: scaledWidth, height: scaledHeight },
    };
  })();

  const touchPositionInNonextendedHomeCoords = {
    x: Math.floor((clientPosition.x - mainRbox.topLeft.x) / scale),
    y: Math.floor((clientPosition.y - mainRbox.topLeft.y) / scale),
  };
  const touchPositionInImageCoords = subVec(
    touchPositionInNonextendedHomeCoords,
    bboxInNonextendedHomeCoords
  );
  const imageX = touchPositionInImageCoords.x;
  const imageY = touchPositionInImageCoords.y;

  if (buttonImage.naturalWidth === 0 || buttonImage.naturalHeight === 0) {
    return false;
  }

  const ctx = resources.tempCanvasCtx;
  ctx.canvas.width = NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.width;
  ctx.canvas.height = NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.height;
  ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
  ctx.drawImage(buttonImage, imageX, imageY, 1, 1, imageX, imageY, 1, 1);
  const imageData = ctx.getImageData(imageX, imageY, 1, 1).data;
  return imageData[3] > 0;
}

