function sum(nums: readonly number[]): number {
  let total = 0;
  for (const n of nums) {
    total += n;
  }
  return total;
}

function getRectCenter(r: Rectangle): Vec2 {
  return {
    x: r.topLeft.x + 0.5 * r.dimensions.width,
    y: r.topLeft.y + 0.5 * r.dimensions.height,
  };
}

function didHeroDefeatAllEnemiesButForgetToMoveToStairs(
  levelState: LevelState,
  hasFailLevelEventBeenEmitted: boolean
): boolean {
  const heroIndex = getHeroIndex(levelState);
  if (heroIndex === -1) {
    return false;
  }

  const { squares } = levelState;

  return (
    (squares[heroIndex] as HeroSquareState).health > 0 &&
    squares
      .slice(heroIndex + 1)
      .every((square) => square.kind === SquareKind.Empty) &&
    hasFailLevelEventBeenEmitted
  );
}

function didHeroSurviveLevelButFailToReachStairs(
  levelState: LevelState,
  hasFailLevelEventBeenEmitted: boolean
): boolean {
  const heroIndex = getHeroIndex(levelState);
  if (heroIndex === -1) {
    return false;
  }

  const { squares } = levelState;

  return (
    (squares[heroIndex] as HeroSquareState).health > 0 &&
    hasFailLevelEventBeenEmitted
  );
}

function containsEnemy(square: SquareState): boolean {
  return [SquareKind.Goblin, SquareKind.GiantGoblin, SquareKind.Witch].includes(
    square.kind
  );
}

function getEnemyCombinationKindOfEnemiesInitiallyBlockingHeroFromStairs(
  level: Level
): "NoEnemies" | EnemyCombinationKind {
  const initialLevelState = getInitialLevelState(level);
  const enemiesInitiallyBetweenHeroAndStairs = initialLevelState.squares
    .slice(getHeroIndex(initialLevelState) + 1)
    .filter(containsEnemy);

  if (enemiesInitiallyBetweenHeroAndStairs.length === 0) {
    return "NoEnemies";
  }

  const everyInitialEnemyWasGoblinOrGiantGoblin =
    enemiesInitiallyBetweenHeroAndStairs.every((square) =>
      [SquareKind.Goblin, SquareKind.GiantGoblin].includes(square.kind)
    );
  const everyInitialEnemyWasWitch = enemiesInitiallyBetweenHeroAndStairs.every(
    (square) => [SquareKind.Witch].includes(square.kind)
  );

  if (
    everyInitialEnemyWasGoblinOrGiantGoblin &&
    enemiesInitiallyBetweenHeroAndStairs.length === 1
  ) {
    return EnemyCombinationKind.SingleGoblin;
  }

  if (
    everyInitialEnemyWasGoblinOrGiantGoblin &&
    enemiesInitiallyBetweenHeroAndStairs.length >= 2
  ) {
    return EnemyCombinationKind.MultipleGoblins;
  }

  if (
    everyInitialEnemyWasWitch &&
    enemiesInitiallyBetweenHeroAndStairs.length === 1
  ) {
    return EnemyCombinationKind.SingleWitch;
  }

  throw new Error("Impossible enemy combination kind for level " + level);
}

function performPreflightSanityCheckIfNotAlreadyPerformed(): void {
  if (hasPerformedPreflightSanityCheck) {
    return;
  }

  performPreflightSanityCheck();

  console.log("Finished preflight sanity check.");
  hasPerformedPreflightSanityCheck = true;
}

function performPreflightSanityCheck(): void {
  performEnemyCombinationSanityCheckOnAllLevels();
}

function performEnemyCombinationSanityCheckOnAllLevels(): void {
  for (let level = Level.Level1; level <= LAST_LEVEL; ++level) {
    // This will throw if the level has a supposedly impossible enemy combination.
    getEnemyCombinationKindOfEnemiesInitiallyBlockingHeroFromStairs(level);
  }
}
